import { getVersionList } from "@/api/modules/miniapp/version";
import FilterHeader from "@/components/FilterHeader";
import { Button, Card, Table, Tag, message } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";

interface DataType {
    versionId: React.Key;
    appId: string;
    release: string;
    env: string;
    createdAt: string;
}

const View: React.FC = () => {
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [versionTotal, setVersionTotal] = useState(0);
    const [eventList, setEventList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: '版本',
            width: 100,
            render: (_: any, record: DataType) => (<div key={record.versionId} style={{ display: 'flex', flexDirection: 'row',alignItems:'center' }}>
                <Tag color="blue">{record.release}</Tag>&nbsp;&nbsp;|&nbsp;&nbsp;{record.env}
            </div>),
        },
        {
            title: '上线时间',
            dataIndex: 'createdAt',
            width: 200,
        },
        {
            title: '操作',
            width: 200,
            render: (_, record:DataType) => (
                <Button type="link">详情</Button>
            ),
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initVersionList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getVersionList({ pageNum, pageSize, appId });
        if (result.data) {
            setVersionTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    versionId: item.versionId,
                    appId: item.appId,
                    release: item.release,
                    env: item.env,
                    createdAt: item.createdAt
                })
            });
            setEventList(data);
        }
    };

    useEffect(() => {
        initVersionList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        initVersionList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize]);

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: versionTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <Card title="版本列表" style={{ marginTop: '16px' }}>
            <Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={eventList} />
        </Card>
    </div>);
}

export default View;